contrast-disabled;\n  }\n}\n","@use \"sass:map\";\n@use \"sass:string\";\n@use \"sass:list\";\n\n@use \"../../functions\" as *;\n@use \"../../variables\" as *;\n@use \"../../tokens/font\" as *;\n@use \"../../settings\" as *;\n\n// Output the @font-face rule\n@mixin at-font-face($display-name, $file-path, $font-weight, $font-style) {\n  // TODO: If $theme-use-rails-pipeline use font-url() statements\n  // instead of url()\n  // Dunno why I can't do this without an error...\n\n  @font-face {\n    font-family: $display-name;\n    font-style: string.unquote($font-style);\n    font-weight: $font-weight;\n    font-display: fallback;\n    src: font-sources($file-path);\n  }\n}\n\n// Loop through weights, then call at-font-face\n@mixin generate-font-face(\n  $font-style-src,\n  $output-weights,\n  $display-name,\n  $dir,\n  $font-style\n) {\n  @each $font-weight, $filename in $font-style-src {\n    @each $key, $output-weight in $output-weights {\n      @if $output-weight == $font-weight and $filename {\n        @include at-font-face(\n          \"#{$display-name}\",\n          // TODO: Why is this path causing problems?\n          \"#{$theme-font-path}/#{$dir}/#{$filename}\",\n          #{$font-weight},\n          string.unquote(\"#{$font-style}\")\n        );\n      }\n    }\n  }\n}\n\n// Collect all font metadata, then call generate-font-face\n@mixin render-font-face($typeface-token, $src) {\n  $generate: false;\n  $this-src: ();\n  $output-weights: $project-font-weights;\n  @if $theme-generate-all-weights {\n    $output-weights: (\n      100: 100,\n      200: 200,\n      300: 300,\n      400: 400,\n      500: 500,\n      600: 600,\n      700: 700,\n      800: 800,\n      900: 900,\n    );\n  }\n\n  $typeface-metadata: map.get($all-typeface-tokens, $typeface-token);\n\n  // If the typeface has src in its USWDS metadata, generate and\n  // set $this-src\n  @if map.get($typeface-metadata, src) {\n    $generate: true;\n    $this-src: map.get($typeface-metadata, src);\n  }\n\n  // If the typeface has custom src defined, generate and override\n  // any existing USWDS src\n  @if $src {\n    $generate: true;\n    $this-src: $src;\n  }\n\n  @if $generate {\n    $display-name: map.get($typeface-metadata, display-name);\n    $roman: map.get($this-src, roman);\n    $italic: map.get($this-src, italic);\n    $dir: map.get($this-src, dir);\n\n    @if $roman {\n      @include generate-font-face(\n        $roman,\n        $output-weights,\n        $display-name,\n        $dir,\n        normal\n      );\n    }\n\n    @if $italic {\n      @include generate-font-face(\n        $italic,\n        $output-weights,\n        $display-name,\n        $dir,\n        italic\n      );\n    }\n  }\n}\n","@use \"uswds-core/src/styles/mixins/general/focus-outline\" as *;\n\n// Apply :focus behavior to focusable elements only (For IE 11)\ninput,\nselect,\ntextarea,\nbutton {\n  &:not([disabled]) {\n    &:focus {\n      @include focus-outline;\n    }\n  }\n}\n\niframe,\n[href],\n[tabindex],\n[contentEditable=\"true\"] {\n  &:focus {\n    @include focus-outline;\n  }\n}\n","@use \"../../functions\" as *;\n@use \"../../settings\" as *;\n\n// Focus state mixin\n@mixin focus-outline(\n  $width: $theme-focus-width,\n  $style: $theme-focus-style,\n  $color: $theme-focus-color,\n  $offset: $theme-focus-offset\n) {\n  $width: if($width == null, $theme-focus-width, $width);\n  $style: if($style == null, $theme-focus-style, $style);\n  $color: if($color == null, $theme-focus-color, $color);\n  $offset: if($offset == null, $theme-focus-offset, $offset);\n  outline: units($width) $style color($color);\n  outline-offset: units($offset);\n}\n","// TODO: Add documentation instructions for using this class on image links\n@mixin media-link {\n  display: inline-block;\n  line-height: 0;\n}\n","@use \"uswds-core/src/styles/mixins/general/focus-outline\" as *;\n\n.usa-focus {\n  @include focus-outline;\n}\n","@use \"uswds-core/src/styles/mixins/general/screen-reader\" as *;\n\n.usa-sr-only {\n  @include sr-only;\n}\n","// Screen reader only helper\n@mixin sr-only {\n  position: absolute;\n  left: -999em;\n  right: auto;\n}\n\n@mixin add-sr-only {\n  @include sr-only;\n}\n\n// This \"negates\" the sr-only helper; useful if\n// we want the default state of something to be\n// for screen readers only, but we want to force\n// it to show for sighted users under more specific\n// conditions.\n@mixin not-sr-only {\n  position: static;\n}\n\n@mixin add-no-sr-only {\n  position: static;\n}\n","@use \"uswds-core\" as *;\n\n.usa-content {\n  p,\n  ul:not(.usa-accordion):not(.usa-accordion--bordered),\n  ol:not(.usa-accordion):not(.usa-accordion--bordered) {\n    max-width: measure($theme-text-measure);\n  }\n}\n","@use \"uswds-core\" as *;\n\n.usa-dark-background {\n  background-color: color(\"base-darker\");\n\n  p,\n  span {\n    color: color($theme-text-reverse-color);\n  }\n\n  a {\n    color: color($theme-link-reverse-color);\n\n    &:visited {\n      color: color($theme-link-reverse-color);\n    }\n\n    &:hover {\n      color: color($theme-link-reverse-hover-color);\n    }\n  }\n}\n","@use \"uswds-core\" as *;\n\n.usa-display {\n  @include typeset-h3;\n  margin-bottom: 0;\n\n  @include at-media(\"mobile-lg\") {\n    @include typeset-h1;\n  }\n\n  @include at-media(\"tablet\") {\n    @include typeset-display;\n  }\n}\n","@use \"sass:list\";\n@use \"../../functions\" as *;\n\n// Adds margin either l/r(x) or t/b(y)\n\n@mixin margin-n($side, $value...) {\n  $value: unpack($value);\n  @if $side == all {\n    $important: null;\n    @if has-important($value) {\n      $value: remove($value, \"!important\");\n      @if list.length($value) == 1 {\n        $value: de-list($value);\n      }\n      $important: \" !important\";\n    }\n    margin: get-uswds-value(margin, $value...) #{$important};\n  } @else if $side == x {\n    $important: null;\n    @if has-important($value) {\n      $value: remove($value, \"!important\");\n      @if list.length($value) == 1 {\n        $value: de-list($value);\n      }\n      $important: \" !important\";\n    }\n    margin-left: get-uswds-value(\"margin-horizontal\", $value...) #{$important};\n    margin-right: get-uswds-value(\"margin-horizontal\", $value...) #{$important};\n  } @else if $side == y {\n    $important: null;\n    @if has-important($value) {\n      $value: remove($value, \"!important\");\n      @if list.length($value) == 1 {\n        $value: de-list($value);\n      }\n      $important: \" !important\";\n    }\n    margin-bottom: get-uswds-value(\"margin-vertical\", $value...) #{$important};\n    margin-top: get-uswds-value(\"margin-vertical\", $value...) #{$important};\n  } @else if $side == t {\n    $important: null;\n    @if has-important($value) {\n      $value: remove($value, \"!important\");\n      @if list.length($value) == 1 {\n        $value: de-list($value);\n      }\n      $important: \" !important\";\n    }\n    margin-top: get-uswds-value(\"margin-vertical\", $value...) #{$important};\n  } @else if $side == r {\n    $important: null;\n    @if has-important($value) {\n      $value: remove($value, \"!important\");\n      @if list.length($value) == 1 {\n        $value: de-list($value);\n      }\n      $important: \" !important\";\n    }\n    margin-right: get-uswds-value(\"margin-horizontal\", $value...) #{$important};\n  } @else if $side == b {\n    $important: null;\n    @if has-important($value) {\n      $value: remove($value, \"!important\");\n      @if list.length($value) == 1 {\n        $value: de-list($value);\n      }\n      $important: \" !important\";\n    }\n    margin-bottom: get-uswds-value(\"margin-vertical\", $value...) #{$important};\n  } @else if $side == l {\n    $important: null;\n    @if has-important($value) {\n      $value: remove($value, \"!important\");\n      @if list.length($value) == 1 {\n        $value: de-list($value);\n      }\n      $important: \" !important\";\n    }\n    margin-left: get-uswds-value(\"margin-horizontal\", $value...) #{$important};\n  }\n}\n\n@mixin u-margin($value...) {\n  @include margin-n(all, $value...);\n}\n\n@mixin u-margin-x($value...) {\n  @include margin-n(x, $value...);\n}\n\n@mixin u-margin-y($value...) {\n  @include margin-n(y, $value...);\n}\n\n@mixin u-margin-top($value...) {\n  @include margin-n(t, $value...);\n}\n\n@mixin u-margin-right($value...) {\n  @include margin-n(r, $value...);\n}\n\n@mixin u-margin-bottom($value...) {\n  @include margin-n(b, $value...);\n}\n\n@mixin u-margin-left($value...) {\n  @include margin-n(l, $value...);\n}\n","@use \"sass:meta\";\n@use \"sass:list\";\n@use \"../general/focus-outline\" as *;\n@use \"../../functions\" as *;\n@use \"../../mixins/utilities\" as *;\n@use \"../../mixins/general/focus-outline\" as *;\n@use \"../../settings\" as *;\n\n/*\n----------------------------------------\ntypeset()\n----------------------------------------\nSets:\n- family\n- size\n- line-height\n----------------------------------------\n*/\n\n@mixin typeset(\n  $family: $theme-body-font-family,\n  $scale: $theme-body-font-size,\n  $line-height: $theme-body-line-height\n) {\n  @if meta.type-of($family) == \"list\" {\n    $list: $family;\n    $family: if(list.nth($list, 1), list.nth($list, 1), null);\n    $scale: if(list.nth($list, 2), list.nth($list, 2), null);\n    $line-height: if(list.nth($list, 3), list.nth($list, 3), null);\n  }\n  $family: if($family == null, $theme-body-font-family, $family);\n  $scale: if($scale == null, $theme-body-font-size, $scale);\n  $line-height: if($line-height == null, $theme-body-line-height, $line-height);\n  @include u-font($family, $scale);\n  @include u-line-height($family, $line-height);\n}\n\n@mixin typeset-heading-base {\n  @include u-margin-y(0);\n  clear: both;\n}\n\n@mixin typeset-heading {\n  @include typeset-heading-base;\n\n  * + & {\n    margin-top: $theme-heading-margin-top;\n  }\n\n  + * {\n    margin-top: $theme-paragraph-margin-top;\n  }\n}\n\n@mixin typeset-p-base {\n  line-height: line-height($theme-body-font-family, $theme-body-line-height);\n  max-width: measure($theme-text-measure);\n}\n\n// typeset element mixins\n@mixin typeset-p {\n  @include typeset-p-base;\n  @include u-margin-y(0);\n\n  * + & {\n    margin-top: $theme-paragraph-margin-top;\n  }\n\n  + * {\n    margin-top: $theme-paragraph-margin-top;\n  }\n}\n\n@mixin typeset-link {\n  color: color($theme-link-color);\n  text-decoration: underline;\n\n  &:visited {\n    color: color($theme-link-visited-color);\n  }\n\n  &:hover {\n    color: color($theme-link-hover-color);\n  }\n\n  &:active {\n    color: color($theme-link-active-color);\n  }\n\n  &:focus {\n    @include focus-outline;\n  }\n}\n","@use \"../../settings\" as *;\n@use \"../../functions\" as *;\n@use \"../../mixins/utilities\" as *;\n@use \"./typeset\" as *;\n\n@mixin display {\n  @include typeset(\n    \"heading\",\n    $theme-display-font-size,\n    $theme-heading-line-height\n  );\n\n  font-weight: fw(\"bold\");\n}\n\n@mixin h1 {\n  @include typeset(\"heading\", $theme-h1-font-size, $theme-heading-line-height);\n  font-weight: fw(\"bold\");\n}\n\n@mixin h2 {\n  @include typeset(\"heading\", $theme-h2-font-size, $theme-heading-line-height);\n  font-weight: fw(\"bold\");\n}\n\n@mixin h3 {\n  @include typeset(\"heading\", $theme-h3-font-size, $theme-heading-line-height);\n  font-weight: fw(\"bold\");\n}\n\n@mixin h4 {\n  @include typeset(\"heading\", $theme-h4-font-size, $theme-heading-line-height);\n  font-weight: fw(\"bold\");\n}\n@mixin h5 {\n  @include typeset(\"heading\", $theme-h5-font-size, $theme-heading-line-height);\n  font-weight: fw(\"bold\");\n}\n\n@mixin h6 {\n  @include typeset(\"body\", $theme-h6-font-size, $theme-heading-line-height);\n  font-weight: fw(\"normal\");\n  letter-spacing: ls(\"ls-1\");\n  text-transform: uppercase;\n}\n\n@mixin typeset-display {\n  @include typeset-heading;\n  @include display;\n}\n\n@mixin typeset-h1 {\n  @include typeset-heading;\n  @include h1;\n}\n\n@mixin typeset-h2 {\n  @include typeset-heading;\n  @include h2;\n}\n\n@mixin typeset-h3 {\n  @include typeset-heading;\n  @include h3;\n}\n\n@mixin typeset-h4 {\n  @include typeset-heading;\n  @include h4;\n}\n\n@mixin typeset-h5 {\n  @include typeset-heading;\n  @include h5;\n}\n\n@mixin typeset-h6 {\n  @include typeset-heading;\n  @include h6;\n}\n","@use \"sass:map\";\n@use \"../../functions\" as *;\n@use \"../../properties\" as *;\n@use \"../../settings\" as *;\n\n// Mobile-first media query helper\n\n@mixin at-media($bp) {\n  $quoted-bp: smart-quote($bp);\n  $our-breakpoints: map-deep-get($system-properties, breakpoints, standard);\n  @if $quoted-bp == \"none\" {\n    @content;\n  } @else if map.has-key($our-breakpoints, $quoted-bp) {\n    @if $theme-respect-user-font-size {\n      $bp: rem-to-user-em(map.get($our-breakpoints, $quoted-bp));\n    } @else {\n      $bp: rem-to-px(map.get($our-breakpoints, $quoted-bp));\n    }\n    @media all and (min-width: #{$bp}) {\n      @content;\n    }\n  } @else {\n    @warn '`#{$bp}` is not a valid USWDS project breakpoint. Valid values: #{map.keys($our-breakpoints)}';\n  }\n}\n\n// Max-width media query\n@mixin at-media-max($bp) {\n  $quoted-bp: smart-quote($bp);\n  $our-breakpoints: map-deep-get($system-properties, breakpoints, standard);\n  @if map-has-key($our-breakpoints, $quoted-bp) {\n    @if $theme-respect-user-font-size {\n      $bp: rem-to-user-em(map.get($our-breakpoints, $quoted-bp)) - 0.01em;\n    } @else {\n      $bp: rem-to-px(map.get($our-breakpoints, $quoted-bp)) - 1px;\n    }\n  } @else {\n    @warn '`#{$bp}` is not a valid USWDS project breakpoint. Valid values: #{map-keys($our-breakpoints)}';\n  }\n  @media all and (max-width: #{$bp}) {\n    @content;\n  }\n}\n","@use \"uswds-core\" as *;\n\n.usa-intro {\n  @include typeset(\n    $theme-lead-font-family,\n    $theme-lead-font-size,\n    $theme-lead-line-height\n  );\n  font-weight: $theme-font-weight-normal;\n  max-width: measure($theme-lead-measure);\n}\n","/*\n* * * * * ==============================\n* * * * * ==============================\n* * * * * ==============================\n* * * * * ==============================\n========================================\n========================================\n========================================\n----------------------------------------\nTYPOGRAPHY SETTINGS\n----------------------------------------\nRead more about settings and\nUSWDS typography tokens in the documentation:\nhttps://designsystem.digital.gov/design-tokens/typesetting/overview/\n----------------------------------------\n*/\n\n/*\n----------------------------------------\nRoot font size\n----------------------------------------\nSetting $theme-respect-user-font-size to\ntrue sets the root font size to 100% and\nuses ems for media queries\n----------------------------------------\n$theme-root-font-size only applies when\n$theme-respect-user-font-size is set to\nfalse.\n\nThis will set the root font size\nas a specific px value and use px values\nfor media queries.\n\nAccepts true or false\n----------------------------------------\n*/\n\n$theme-respect-user-font-size: true !default;\n\n// $theme-root-font-size only applies when\n// $theme-respect-user-font-size is set to\n// false.\n\n// This will set the root font size\n// as a specific px value and use px values\n// for media queries.\n\n// Accepts values in px\n\n$theme-root-font-size: 10px !default;\n\n/*\n----------------------------------------\nGlobal styles\n----------------------------------------\nAdds basic styling for the following\nunclassed elements:\n\n- paragraph: paragraph text\n- link: links\n- content: paragraph text, links,\n  headings, lists, and tables\n----------------------------------------\n*/\n\n$theme-global-paragraph-styles: false !default;\n$theme-global-link-styles: false !default;\n$theme-global-content-styles: false !default;\n\n/*\n----------------------------------------\nBroswer compatibility mode\n----------------------------------------\nWhen true, outputs woff and ttf font \nformats in addition to woff2\n----------------------------------------\n*/\n\n$theme-font-browser-compatibility: false !default;\n\n/*\n----------------------------------------\nFont path\n----------------------------------------\nRelative font file path\n----------------------------------------\n*/\n\n$theme-font-path: \"../fonts\" !default;\n\n/*\n----------------------------------------\nCustom typeface tokens\n----------------------------------------\nAdd a new custom typeface token if\nyour project uses a typeface not already\ndefined by USWDS.\n----------------------------------------\nUSWDS defines the following tokens\nby default:\n----------------------------------------\n'georgia'\n'helvetica'\n'merriweather'\n'open-sans'\n'public-sans'\n'roboto-mono'\n'source-sans-pro'\n'system'\n'tahoma'\n'verdana'\n----------------------------------------\nAdd as many new tokens as you have\ncustom typefaces. Reference your new\ntoken(s) in the type-based font settings\nusing the quoted name of the token.\n\nFor example:\n\n$theme-font-type-cond: 'example-font-token';\n\ndisplay-name:\nThe display name of your font\n\ncap-height:\nThe height of a 500px `N` in Sketch\n----------------------------------------\nYou should change `example-[style]-token`\nnames to something more descriptive.\n----------------------------------------\n*/\n\n$theme-typeface-tokens: (\n  example-serif-token: (\n    display-name: \"Example Serif Display Name\",\n    cap-height: 364px,\n  ),\n  example-sans-token: (\n    display-name: \"Example Sans Display Name\",\n    cap-height: 364px,\n  ),\n) !default;\n\n/*\n----------------------------------------\nType-based font settings\n----------------------------------------\nSet the type-based tokens for your\nproject from the following tokens,\nor from any new font tokens you added in\n$theme-typeface-tokens.\n----------------------------------------\n'georgia'\n'helvetica'\n'merriweather'\n'open-sans'\n'public-sans'\n'roboto-mono'\n'source-sans-pro'\n'system'\n'tahoma'\n'verdana'\n----------------------------------------\n*/\n\n// condensed\n$theme-font-type-cond: false !default;\n\n// icon\n$theme-font-type-icon: false !default;\n\n// language-specific\n$theme-font-type-lang: false !default;\n\n// monospace\n$theme-font-type-mono: \"roboto-mono\" !default;\n\n// sans-serif\n$theme-font-type-sans: \"source-sans-pro\" !default;\n\n// serif\n$theme-font-type-serif: \"merriweather\" !default;\n\n/*\n----------------------------------------\nCustom font stacks\n----------------------------------------\nAdd custom font stacks to any of the\ntype-based fonts. Any USWDS typeface\ntoken already has a default stack.\n\nCustom stacks don't need to include the\nfont's display name. It will\nautomatically appear at the start of\nthe stack.\n----------------------------------------\nExample:\n$theme-font-type-sans: 'source-sans-pro';\n$theme-font-sans-custom-stack: \"Helvetica Neue\", Helvetica, Arial, sans;\n\nOutput:\nfont-family: \"Source Sans Pro\", \"Helvetica Neue\", Helvetica, Arial, sans;\n----------------------------------------\n*/\n\n$theme-font-cond-custom-stack: false !default;\n$theme-font-icon-custom-stack: false !default;\n$theme-font-lang-custom-stack: false !default;\n$theme-font-mono-custom-stack: false !default;\n$theme-font-sans-custom-stack: false !default;\n$theme-font-serif-custom-stack: false !default;\n\n/*\n----------------------------------------\nAdd any custom font source files\n----------------------------------------\nIf you want USWDS to generate additional\n@font-face declarations, add your font\ndata below, following the example that\nfollows.\n----------------------------------------\nUSWDS automatically generates @font-face\ndeclarations for the following\n\n'merriweather'\n'public-sans'\n'roboto-mono'\n'source-sans-pro'\n\nThese typefaces not require custom\nsource files.\n----------------------------------------\nEXAMPLE\n\n- dir:\n  Directory relative to $theme-font-path\n- This directory should include fonts saved as\n  .woff2\n  ExampleSerif-Normal.woff2\n\n$theme-font-serif-custom-src: (\n  dir: 'custom/example-serif',\n  roman: (\n    100: false,\n    200: false,\n    300: 'ExampleSerif-Light',\n    400: 'ExampleSerif-Normal',\n    500: false,\n    600: false,\n    700: 'ExampleSerif-Bold',\n    800: false,\n    900: false,\n  ),\n  italic: (\n    100: false,\n    200: false,\n    300: 'ExampleSerif-LightItalic',\n    400: 'ExampleSerif-Italic',\n    500: false,\n    600: false,\n    700: 'ExampleSerif-BoldItalic',\n    800: false,\n    900: false,\n  ),\n);\n----------------------------------------\n*/\n\n$theme-font-cond-custom-src: false !default;\n$theme-font-icon-custom-src: false !default;\n$theme-font-lang-custom-src: false !default;\n$theme-font-mono-custom-src: false !default;\n$theme-font-sans-custom-src: false !default;\n$theme-font-serif-custom-src: false !default;\n\n/*\n----------------------------------------\nRole-based font settings\n----------------------------------------\nSet the role-based tokens for your\nproject from the following font-type\ntokens.\n----------------------------------------\n'cond'\n'icon'\n'lang'\n'mono'\n'sans'\n'serif'\n----------------------------------------\n*/\n\n$theme-font-role-ui: \"sans\" !default;\n$theme-font-role-heading: \"serif\" !default;\n$theme-font-role-body: \"sans\" !default;\n$theme-font-role-code: \"mono\" !default;\n$theme-font-role-alt: \"serif\" !default;\n\n/*\n----------------------------------------\nType scale\n----------------------------------------\nDefine your project's type scale using\nvalues from the USWDS system type scale\n\n1-20\n----------------------------------------\n*/\n\n$theme-type-scale-3xs: 2 !default;\n$theme-type-scale-2xs: 3 !default;\n$theme-type-scale-xs: 4 !default;\n$theme-type-scale-sm: 5 !default;\n$theme-type-scale-md: 6 !default;\n$theme-type-scale-lg: 9 !default;\n$theme-type-scale-xl: 12 !default;\n$theme-type-scale-2xl: 14 !default;\n$theme-type-scale-3xl: 15 !default;\n\n/*\n----------------------------------------\nFont weights\n----------------------------------------\nAssign weights 100-900\nOr use `false` for unneeded weights.\n----------------------------------------\n*/\n\n$theme-font-weight-thin: false !default;\n$theme-font-weight-light: 300 !default;\n$theme-font-weight-normal: 400 !default;\n$theme-font-weight-medium: false !default;\n$theme-font-weight-semibold: false !default;\n$theme-font-weight-bold: 700 !default;\n$theme-font-weight-heavy: false !default;\n\n// If USWDS is generating your @font-face rules,\n// should we generate all available weights\n// regardless of the assignments above?\n\n$theme-generate-all-weights: false !default;\n\n/*\n----------------------------------------\nGeneral typography settings\n----------------------------------------\nType scale tokens\n----------------------------------------\nmicro:      10px\n1:          12px\n2:          13px\n3:          14px\n4:          15px\n5:          16px\n6:          17px\n7:          18px\n8:          20px\n9:          22px\n10:         24px\n11:         28px\n12:         32px\n13:         36px\n14:         40px\n15:         48px\n16:         56px\n17:         64px\n18:         80px\n19:         120px\n20:         140px\n----------------------------------------\nLine height tokens\n----------------------------------------\n1:    1\n2:    1.15\n3:    1.35\n4:    1.5\n5:    1.62\n6:    1.75\n----------------------------------------\nFont role tokens\n----------------------------------------\n'ui'\n'heading'\n'body'\n'code'\n'alt'\n----------------------------------------\nMeasure (max-width) tokens\n----------------------------------------\n1:       44ex\n2:       60ex\n3:       64ex\n4:       68ex\n5:       74ex\n6:       88ex\nnone:    none\n----------------------------------------\n*/\n\n// Body settings are the equivalent of setting the <body> element\n$theme-body-font-family: \"body\" !default;\n$theme-body-font-size: \"sm\" !default;\n$theme-body-line-height: 5 !default;\n\n// If true, explicitly style the <body> element with the base styles\n$theme-style-body-element: false !default;\n\n// Headings\n$theme-h1-font-size: \"2xl\" !default;\n$theme-h2-font-size: \"xl\" !default;\n$theme-h3-font-size: \"lg\" !default;\n$theme-h4-font-size: \"sm\" !default;\n$theme-h5-font-size: \"xs\" !default;\n$theme-h6-font-size: \"3xs\" !default;\n$theme-heading-line-height: 2 !default;\n$theme-small-font-size: \"2xs\" !default;\n$theme-display-font-size: \"3xl\" !default;\n\n// Text and prose\n$theme-text-measure-narrow: 1 !default;\n$theme-text-measure: 4 !default;\n$theme-text-measure-wide: 6 !default;\n$theme-prose-font-family: \"body\" !default;\n\n// Lead text\n$theme-lead-font-family: \"heading\" !default;\n$theme-lead-font-size: \"lg\" !default;\n$theme-lead-line-height: 6 !default;\n$theme-lead-measure: 6 !default;\n\n// Margin\n$theme-heading-margin-top: 1.5em !default;\n$theme-paragraph-margin-top: 1em !default;\n","@use \"uswds-core\" as *;\n\n$tokens-icon-sizes: (\n  \"3\": 3,\n  \"4\": 4,\n  \"5\": 5,\n  \"6\": 6,\n  \"7\": 7,\n  \"8\": 8,\n  \"9\": 9,\n);\n\n$icon-inline-size-magic-number: 1em;\n\n.usa-icon {\n  display: inline-block;\n  fill: currentColor;\n  height: $icon-inline-size-magic-number;\n  position: relative;\n  width: $icon-inline-size-magic-number;\n}\n\n@each $token, $units in $tokens-icon-sizes {\n  .usa-icon--size-#{$units} {\n    @include u-square($units);\n  }\n}\n","@use \"sass:list\";\n@use \"../../functions\" as *;\n\n// Outputs height\n\n@mixin u-height($value...) {\n  $value: unpack($value);\n  $important: null;\n  @if has-important($value) {\n    $value: remove($value, \"!important\");\n    @if list.length($value) == 1 {\n      $value: de-list($value);\n    }\n    $important: \" !important\";\n  }\n  height: get-uswds-value(\"height\", $value...) #{$important};\n}\n","@use \"sass:list\";\n@use \"../../functions\" as *;\n\n// Outputs width\n\n@mixin u-width($value...) {\n  $value: unpack($value);\n  $important: null;\n  @if has-important($value) {\n    $value: remove($value, \"!important\");\n    @if list.length($value) == 1 {\n      $value: de-list($value);\n    }\n    $important: \" !important\";\n  }\n  width: get-uswds-value(\"width\", $value...) #{$important};\n}\n","@use \"uswds-core\" as *;\n\n.usa-link {\n  @include typeset-link;\n}\n\n// External link consider 'effortless style approach':\n// [href^='http:']:not([href*='my-domain.com'])\n// [href^='https:']:not([href*='my-domain.com'])\n\n.usa-link--external {\n  @include external-link();\n\n  &.usa-link--alt {\n    @include external-link($contrast-bg: \"base-darker\");\n  }\n}\n","@use \"../../settings\" as *;\n@use \"./icon\" as *;\n@use \"../../functions\" as *;\n@use \"./screen-reader\" as *;\n\n$external-link-size: 1.75ex;\n\n$icon-object: (\n  \"name\": \"launch\",\n  \"color\": currentColor,\n  \"height\": $external-link-size,\n  \"svg-height\": 24,\n  \"svg-width\": 24,\n  \"position-x\": center,\n  \"position-y\": center,\n);\n\n@mixin external-link($contrast-bg: \"default\") {\n  display: inline;\n\n  // Screen reader label\n  &::before {\n    @include sr-only;\n    content: $theme-external-link-sr-label-tab-same;\n  }\n\n  &[target=\"_blank\"]::before {\n    @include sr-only;\n    content: $theme-external-link-sr-label-tab-new;\n  }\n\n  &::after {\n    @include add-color-icon($icon-object, $contrast-bg);\n    content: \"\";\n    display: inline;\n    margin-top: 0.7ex;\n    margin-left: 2px;\n    padding-left: 1.75ex;\n    vertical-align: middle;\n  }\n}\n","@use \"sass:map\";\n@use \"sass:math\";\n\n@use \"../../settings\" as *;\n@use \"../../functions\" as *;\n@use \"../../variables\" as *;\n@use \"./add-background-svg\" as *;\n\n@mixin display-icon($icon, $direction, $size, $margin, $hover) {\n  &::#{$direction} {\n    @include add-background-svg(\"#{$icon}\");\n    background-size: 100%;\n    content: \"\";\n    display: inline-block;\n    height: $size;\n    width: $size;\n\n    /* stylelint-disable block-closing-brace-newline-after, at-rule-empty-line-before */\n    @if $direction == \"after\" {\n      margin-left: $margin;\n    } @else {\n      margin-right: $margin;\n    }\n    /* stylelint-enable */\n  }\n\n  @if $hover == \"hover\" {\n    &:hover::#{$direction} {\n      @include add-background-svg(\"#{$icon}-hover\");\n    }\n  }\n}\n\n@mixin remove-icon($direction) {\n  &::#{$direction} {\n    display: none;\n  }\n}\n\n@mixin add-icon(\n  $icon-name,\n  $direction,\n  $image-size,\n  $container-size,\n  $margin,\n  $hover\n) {\n  &::#{$direction} {\n    @include add-background-svg(\"#{$icon-name}\");\n    background-position: center center;\n    background-repeat: no-repeat;\n    background-size: units($image-size);\n    content: \"\";\n    display: inline-block;\n    height: units($container-size);\n    width: units($container-size);\n\n    /* stylelint-disable block-closing-brace-newline-after, at-rule-empty-line-before */\n    @if $direction == \"after\" {\n      margin-left: units($margin);\n    } @else {\n      margin-right: units($margin);\n    }\n    /* stylelint-enable */\n  }\n\n  @if $hover == \"hover\" {\n    &:hover::#{$direction} {\n      @include add-background-svg(\"#{$icon-name}-hover\");\n    }\n  }\n}\n\n// New icon mixins using SVG mask technique\n//\n// $image may be a specific SVG or a list in the form\n// ($base, $variant, $variant-alt, $bg)\n// the mixin will pick IE11-compatible svgs named\n// [base]-[variant].svg based on the specified background-color\n//\n// @param {Map} - $icon-object - name, svg-height, svg-width, height, container-height, container-width, color, color-variant, color-hover, rotate, path, position-x, position-y\n// @param {String} - $contrast-bg - Color token\n@mixin add-color-icon($icon-object, $contrast-bg: \"default\") {\n  $filename-base: map.get($icon-object, \"name\");\n  $svg-height: map.get($icon-object, \"svg-height\");\n  $svg-width: map.get($icon-object, \"svg-width\");\n  $aspect: math.div($svg-width, $svg-height);\n  $height: if(\n    math.is-unitless(map.get($icon-object, \"height\")),\n    units(map.get($icon-object, \"height\")),\n    map.get($icon-object, \"height\")\n  );\n  $width: $height * $aspect;\n  $container-height: if(\n    map.has-key($icon-object, \"container-height\"),\n    units(map.get($icon-object, \"container-height\")),\n    null\n  );\n  $container-width: if(\n    map.has-key($icon-object, \"container-width\"),\n    units(map.get($icon-object, \"container-width\")),\n    null\n  );\n  $color: if(\n    map.has-key($icon-object, \"color\"),\n    map.get($icon-object, \"color\"),\n    \"ink\"\n  );\n  $color-variant: if(\n    map.has-key($icon-object, \"color-variant\"),\n    map.get($icon-object, \"color-variant\"),\n    \"white\"\n  );\n  $color-hover: if(\n    map.has-key($icon-object, \"color-hover\"),\n    map.get($icon-object, \"color-hover\"),\n    null\n  );\n  $rotate: if(\n    map.has-key($icon-object, \"rotate\"),\n    map.get($icon-object, \"rotate\"),\n    null\n  );\n  $path: if(\n    map.has-key($icon-object, \"path\"),\n    map.get($icon-object, \"path\"),\n    $theme-image-path\n  );\n  $position-y: if(\n    map.has-key($icon-object, \"position-y\"),\n    map.get($icon-object, \"position-y\"),\n    center\n  );\n  $position-x: if(\n    map.has-key($icon-object, \"position-x\"),\n    map.get($icon-object, \"position-x\"),\n    center\n  );\n  $ie11-variant: get-color-token-from-bg($contrast-bg, \"white\", \"black\");\n  $filename-ie11: if(\n    $ie11-variant == \"white\",\n    \"usa-icons-bg/#{$filename-base}--white.svg\",\n    \"usa-icons/#{$filename-base}.svg\"\n  );\n\n  background-image: url(\"#{$path}/#{$filename-ie11}\");\n  background-repeat: no-repeat;\n  background-position: $position-x $position-y;\n  background-size: $width $height;\n  display: inline-block;\n  height: if($container-height, $container-height, $height);\n  width: if($container-width, $container-width, $width);\n  @if $rotate {\n    transform: rotate($rotate);\n  }\n\n  // Mask supported styles\n  @supports (mask: url(\"\")) {\n    background: none;\n    background-color: if($color == currentColor, $color, color($color));\n    mask-image: url(\"#{$path}/usa-icons/#{$filename-base}.svg\"),\n      linear-gradient(transparent, transparent);\n    mask-position: $position-x $position-y;\n    mask-repeat: no-repeat;\n    mask-size: $width $height;\n\n    @if $color-hover {\n      &:hover {\n        background-color: color($color-hover);\n      }\n    }\n  }\n}\n\n// Places an icon before or after an element as an inline-block,\n// using the `:before` or `:after` pseudoelements.\n@mixin place-icon(\n  $icon-object,\n  $direction,\n  $margin: \"auto\",\n  $vertical-align: middle,\n  $contrast-bg: \"default\"\n) {\n  $color-hover: if(\n    map.has-key($icon-object, \"color-hover\"),\n    map.get($icon-object, \"color-hover\"),\n    null\n  );\n  &::#{$direction} {\n    @include add-color-icon($icon-object, $contrast-bg);\n    content: \"\";\n    vertical-align: $vertical-align;\n\n    @if $direction == \"after\" {\n      margin-left: units($margin);\n    } @else {\n      margin-right: units($margin);\n    }\n  }\n\n  @if $color-hover {\n    &:hover::#{$direction} {\n      content: \"\"; // Added to address a weird display bug\n      background-color: color($color-hover);\n    }\n  }\n}\n","@use \"uswds-core\" as *;\n\n.usa-list {\n  @include usa-list;\n\n  li {\n    @include usa-list-item;\n  }\n}\n\n// Unstyled lists\n@include override-prose {\n  .usa-list--unstyled {\n    @include unstyled-list;\n  }\n}\n","@use \"../../functions\" as *;\n@use \"../../mixins/utilities\" as *;\n@use \"../../settings\" as *;\n\n@mixin usa-list {\n  @include u-margin-y(1em);\n  line-height: line-height($theme-body-font-family, $theme-body-line-height);\n  padding-left: 3ch;\n\n  &:last-child {\n    margin-bottom: 0;\n  }\n\n  ul,\n  ol