;\n    }\n  }\n}\n\n.usa-input-prefix:has(+ input:disabled),\n.usa-input-prefix:has(+ input[aria-disabled=\"true\"]) {\n  @include u-disabled;\n}\n\n.usa-input-prefix,\n.usa-input-suffix {\n  position: absolute;\n  color: color(\"base\");\n  line-height: 0;\n  padding: 0 units($input-padding);\n  user-select: none;\n  white-space: nowrap;\n\n  .usa-icon {\n    @include u-square($icon-width);\n  }\n}\n\n.usa-input-prefix + input {\n  @include u-padding-left($icon-offset);\n  @include u-padding-right($input-padding);\n}\n\n.usa-input-suffix {\n  right: 0;\n}\n","@use \"uswds-core\" as *;\n\n.usa-input-mask {\n  display: block;\n  position: relative;\n}\n\n.usa-input-mask--content {\n  border: 1px solid transparent;\n  color: color(\"gray-50\");\n  display: block;\n  left: 2px;\n  pointer-events: none;\n  position: absolute;\n  top: 0;\n\n  i {\n    visibility: hidden;\n  }\n}\n\n.usa-masked,\n.usa-input-mask--content {\n  @include border-box-sizing;\n  @include u-disabled;\n  background-color: transparent;\n  padding: units(1);\n}\n","@use \"uswds-core\" as *;\n\n.usa-label {\n  @include typeset(\n    $theme-form-font-family,\n    $theme-body-font-size,\n    $theme-input-line-height\n  );\n  display: block;\n  font-weight: font-weight(\"normal\");\n  margin-top: units(3);\n  max-width: units($theme-input-max-width);\n}\n\n.usa-label--error {\n  font-weight: font-weight(\"bold\");\n  margin-top: 0;\n}\n\n.usa-label--required {\n  color: color(\"error-dark\");\n}\n","@use \"uswds-core\" as *;\n\n.usa-memorable-date {\n  display: flex;\n\n  [type=\"number\"] {\n    -moz-appearance: textfield;\n\n    &::-webkit-inner-spin-button {\n      appearance: none;\n    }\n\n    &::-webkit-contacts-auto-fill-button {\n      visibility: hidden;\n      display: none !important;\n      /* stylelint-disable-line declaration-no-important */\n      pointer-events: none;\n      height: 0;\n      width: 0;\n      margin: 0;\n    }\n  }\n\n  .usa-form-group {\n    margin-top: units(2);\n  }\n}\n\n.usa-form-group--day,\n.usa-form-group--month,\n.usa-form-group--year {\n  @include u-flex(auto);\n  margin-right: units(2);\n  width: fit-content;\n}\n\n// Support months that include a text input...\n.usa-form-group--day input,\n.usa-form-group--month input {\n  width: units(6);\n}\n\n// ...and support months that include a select\n.usa-form-group--month select {\n  width: units(card-lg);\n}\n\n.usa-form-group--year input {\n  width: units(9);\n}\n","@use \"sass:math\";\n\n@use \"uswds-core\" as *;\n\n@include radio-colors;\n\n.usa-radio__input {\n  // The actual input element is only visible to screen readers, because\n  // all visual styling is done via the label.\n  @include sr-only();\n\n  &:focus {\n    @include format-input {\n      @include focus-outline(null, null, null, 0.5);\n    }\n  }\n\n  &:disabled,\n  &[aria-disabled=\"true\"] {\n    @include format-input {\n      cursor: not-allowed;\n    }\n    @include format-label {\n      cursor: not-allowed;\n    }\n  }\n\n  // Checkboxes and radios with tap-friendly targets\n  &--tile {\n    @include format-label {\n      border-radius: radius($theme-input-tile-border-radius);\n      margin-top: units(1);\n      padding: units(1.5) units(2) units(1.5) units(5);\n\n      &::before {\n        left: units(5) - units($input-select-margin-right) -\n          units($theme-input-select-size);\n      }\n    }\n  }\n}\n\n.usa-radio__input {\n  &:checked {\n    @include format-input {\n      @media print {\n        box-shadow: inset 0 0 0 units($theme-input-select-border-width)\n            color(\"white\"),\n          inset 0 0 0 units(2) color(\"primary\"),\n          0 0 0 units($theme-input-select-border-width) color(\"primary\");\n      }\n    }\n  }\n}\n\n.usa-radio__label {\n  @extend %block-input-general;\n  cursor: pointer;\n  display: inherit;\n  font-weight: font-weight(\"normal\");\n  margin-top: units(1.5);\n  padding-left: units($input-select-margin-right) +\n    units($theme-input-select-size);\n  position: relative;\n\n  &::before {\n    content: \" \";\n    display: block;\n    left: 0;\n    margin-left: units($theme-input-select-border-width);\n    margin-top: math.div(\n      line-height($theme-form-font-family, $theme-input-line-height) *\n        font-size($theme-form-font-family, $theme-body-font-size) -\n        units($theme-input-select-size),\n      2\n    );\n    position: absolute;\n  }\n}\n\n.usa-radio__label::before {\n  @include u-circle($theme-input-select-size);\n}\n\n.usa-radio__label-description {\n  display: block;\n  font-size: size(\"ui\", \"2xs\");\n  margin-top: units(1);\n}\n\n// Test code for scoped custom colors\n/*\n.radio-tests {\n  @include set-text-and-bg(\"green-80\");\n  @include radio-colors(\"green-80\", \"green-warm-10v\");\n  padding: units(2);\n  border-radius: radius(\"md\");\n}\n*/\n","@use \"uswds-core\" as *;\n\n@mixin range-focus {\n  background-color: color(\"white\");\n  box-shadow: 0 0 0 units(2px) color($theme-focus-color);\n}\n\n@mixin range-track {\n  background-color: color(\"base-lightest\");\n  border-radius: radius(\"pill\");\n  border: units(1px) solid color(\"base\");\n  cursor: pointer;\n  height: units(2);\n  width: 100%;\n}\n\n@mixin range-thumb {\n  @include u-circle($theme-input-select-size);\n  background: color(\"base-lightest\");\n  border: none;\n  box-shadow: 0 0 0 units($theme-input-select-border-width) color(\"base\");\n  cursor: pointer;\n\n  @media (forced-colors: active) {\n    outline: $border-high-contrast;\n  }\n}\n\n@mixin range-ms-fill {\n  background-color: color(\"base-lightest\");\n  border-radius: radius(\"pill\");\n  border: units(1px) solid color(\"base\");\n}\n\n.usa-range {\n  @extend %block-input-general;\n  @extend %block-input-styles;\n  appearance: none;\n  border: none;\n  padding-left: 1px;\n  width: 100%;\n\n  &:focus {\n    outline: none;\n\n    &::-webkit-slider-thumb {\n      @include range-focus;\n    }\n\n    &::-moz-range-thumb {\n      @include range-focus;\n    }\n\n    &::-ms-thumb {\n      @include range-focus;\n    }\n  }\n\n  &::-webkit-slider-runnable-track {\n    @include range-track;\n  }\n\n  &::-moz-range-track {\n    @include range-track;\n  }\n\n  &::-ms-track {\n    @include range-track;\n  }\n\n  &::-webkit-slider-thumb {\n    @include range-thumb;\n    appearance: none;\n    margin-top: px-to-rem(-3px); // magic number\n  }\n\n  &::-moz-range-thumb {\n    @include range-thumb;\n  }\n\n  &::-ms-thumb {\n    @include range-thumb;\n  }\n\n  &::-ms-fill-lower {\n    @include range-ms-fill;\n  }\n\n  &::-ms-fill-upper {\n    @include range-ms-fill;\n  }\n\n  &:disabled,\n  &[aria-disabled=\"true\"] {\n    // Required for mobile styles\n    opacity: 1;\n\n    &::-webkit-slider-runnable-track {\n      @include u-disabled;\n      @include u-disabled-high-contrast-border;\n    }\n\n    &::-moz-range-track {\n      @include u-disabled;\n      @include u-disabled-high-contrast-border;\n    }\n\n    &::-ms-track {\n      @include u-disabled;\n    }\n\n    &::-webkit-slider-thumb {\n      @include u-disabled;\n    }\n\n    &::-moz-range-thumb {\n      @include u-disabled;\n    }\n\n    &::-ms-thumb {\n      @include u-disabled;\n    }\n\n    &::-ms-fill-lower {\n      @include u-disabled;\n    }\n\n    &::-ms-fill-upper {\n      @include u-disabled;\n    }\n  }\n}\n","@use \"uswds-core\" as *;\n\n.usa-textarea {\n  @extend %block-input-general;\n  @extend %block-input-styles;\n\n  &:disabled,\n  &[aria-disabled=\"true\"] {\n    @include u-disabled;\n    @include u-disabled-high-contrast-border;\n  }\n}\n\n.usa-textarea {\n  height: units(\"card\");\n}\n",".usa-time-picker {\n  width: 10em;\n}\n","/*\n========================================\nASPECT\n----------------------------------------\nusage:\n  .aspect-[value]\n----------------------------------------\noutput:\n  padding-left: 0;\n  padding-right: 0;\n  padding-top: 0;\n  padding-bottom: 100%;\n----------------------------------------\nexample:\n  .aspect-16x9\n----------------------------------------\n*/\n@use \"sass:map\";\n@use \"uswds-core/src/styles/settings\" as *;\n@use \"uswds-core/src/styles/variables/aspect-ratios\" as *;\n@use \"uswds-core/src/styles/mixins/general/add-aspect\" as *;\n\n$add-aspect: (\n  add-aspect: (\n    base: \"add-aspect\",\n    modifiers: null,\n    values: (\n      9x16: (\n        slug: \"9x16\",\n        isReadable: true,\n      ),\n      1x1: (\n        slug: \"1x1\",\n        isReadable: true,\n      ),\n      4x3: (\n        slug: \"4x3\",\n        isReadable: true,\n      ),\n      16x9: (\n        slug: \"16x9\",\n        isReadable: true,\n      ),\n      2x1: (\n        slug: \"2x1\",\n        isReadable: true,\n      ),\n    ),\n    settings: $add-aspect-settings-complete,\n    property: \"position\",\n    type: \"object\",\n  ),\n);\n\n@each $aspect-key in map.keys($project-aspect-ratios) {\n  .add-aspect-#{$aspect-key} {\n    @include add-aspect($aspect-key, \"image\");\n  }\n}\n\n// Safari and firefox require a width attribute on img elements for aspect-ratio to work\n// using :where() to reduce specificity in case a width utility class is added\n@supports (aspect-ratio: 1) {\n  :where(img[class*=\"add-aspect-\"]) {\n    width: min-content;\n  }\n}\n","/* stylelint-disable max-nesting-depth */\n\n@use \"sass:map\";\n@use \"sass:meta\";\n@use \"sass:string\";\n@use \"sass:list\";\n\n@use \"../settings\" as *;\n@use \"../properties\" as *;\n@use \"../functions\" as *;\n@use \"../variables/separator\" as *;\n@use \"./helpers\" as *;\n\n/*\n----------------------------------------\n@render-pseudoclass\n----------------------------------------\nBuild a pseucoclass utiliy from values\ncalculated in the @render-utilities-in\nloop\n----------------------------------------\n*/\n\n@mixin render-pseudoclass(\n  $utility,\n  $pseudoclass,\n  $selector,\n  $property,\n  $value,\n  $media-prefix\n) {\n  $important: if($utilities-use-important, \" !important\", null);\n  $this-mq: null;\n\n  .#{$media-prefix}#{$pseudoclass}#{$separator}#{ns(\"utility\")}#{$selector}:#{$pseudoclass} {\n    @each $this-property in $property {\n      #{$this-property}: string.unquote(\"#{$value}#{$important}\");\n    }\n  }\n}\n\n// utility-feature? utility-property\n@mixin add-utility-declaration($declaration, $utility-type, $important) {\n  @each $ext-prop, $ext-value in map.get($declaration, $utility-type) {\n    #{$ext-prop}: string.unquote(\"#{$ext-value}#{$important}\");\n  }\n}\n\n/*\n----------------------------------------\n@render-utility\n----------------------------------------\nBuild a utility from values calculated\nin the @render-utilities-in loop\n----------------------------------------\nTODO: Determine the proper use of\nunquote() in the following. Changed to\naccount for a 'interpolation near\noperators will be simplified in a\nfuture version of Sass' warning.\n----------------------------------------\n*/\n\n@mixin render-utility(\n  $utility,\n  $selector,\n  $property,\n  $value,\n  $val-props,\n  $media-key\n) {\n  $important: if($utilities-use-important, \" !important\", null);\n  $media-prefix: null;\n  $value-is-map: if(meta.type-of($val-props) == \"map\", true, false);\n\n  @if $media-key {\n    $media-prefix: #{$media-key}#{$separator};\n  }\n\n  .#{$media-prefix}#{ns(\"utility\")}#{$selector} {\n    @if $value-is-map and map.has-key($val-props, extend) {\n      @include add-utility-declaration($val-props, extend, $important);\n    }\n\n    @if $value-is-map and map.has-key($val-props, extends) {\n      @extend %#{map.get($val-props, extends)};\n    }\n\n    @each $this-property in $property {\n      #{$this-property}: string.unquote(\"#{$value}#{$important}\");\n    }\n\n    @if map.has-key($utility, extend) {\n      @include add-utility-declaration($utility, extend, $important);\n    }\n  }\n\n  // Add the pseudoclass variants, if applicable\n\n  @if map-deep-get($utility, settings, hover) {\n    @include render-pseudoclass(\n      $utility,\n      hover,\n      $selector,\n      $property,\n      $value,\n      $media-prefix\n    );\n  }\n\n  @if map-deep-get($utility, settings, active) {\n    @include render-pseudoclass(\n      $utility,\n      active,\n      $selector,\n      $property,\n      $value,\n      $media-prefix\n    );\n  }\n\n  @if map-deep-get($utility, settings, visited) {\n    @include render-pseudoclass(\n      $utility,\n      visited,\n      $selector,\n      $property,\n      $value,\n      $media-prefix\n    );\n  }\n\n  @if map-deep-get($utility, settings, focus) {\n    @include render-pseudoclass(\n      $utility,\n      focus,\n      $selector,\n      $property,\n      $value,\n      $media-prefix\n    );\n  }\n}\n\n/*\n----------------------------------------\n@render-utilities-in\n----------------------------------------\nThe master loop that sets the building\nblocks of utilities from the values\nin individual rule settings and loops\nthrough all possible variants\n----------------------------------------\n*/\n\n@mixin these-utilities($utilities, $media-key: false) {\n  // loop through the $utilities\n  @each $utility-name, $utility in $utilities {\n    // Check to see if the utility is in the output uselist\n    @if ($output-these-utilities == \"default\") or\n      list.index($output-these-utilities, $utility-name)\n    {\n      // Only do this if the the utility is meant to output\n      @if not($media-key) or\n        ($media-key and map-deep-get($utility, settings, responsive))\n      {\n        @if map-deep-get($utility, settings, output) {\n          // set intital variants\n          // $property-default is a single value for all these utilities\n\n          $base-props: null;\n          $modifier: null;\n          $selector: null;\n          $property-default: map.get($utility, property);\n          $property: null;\n          $value: null;\n          $our-modifiers: ();\n          $b: null;\n          $v: null;\n          $mv: null;\n          $val-props: ();\n          $no-value: false;\n\n          $b: map.get($utility, base);\n\n          // Each utility rule takes a value, so let's start here\n          // and begin building.\n\n          // -------- For each value in utility.values ----------\n\n          @each $val-key, $val-value in map.get($utility, values) {\n            // If $val-value == null, or if $val-value is a map and\n            // the content key or the dependency key has a null value\n            // set $val-value to `false`...\n\n            @if meta.type-of($val-value) == \"map\" {\n              @if not map.get($val-value, content) {\n                $val-value: false;\n              } @else if\n                map.has-key($val-value, dependency) and not\n                map.get($val-value, dependency)\n              {\n                $val-value: false;\n              }\n            }\n\n            // ...so we can skip building this rule altogether.\n            // So, if $val-value is _not_ false...\n\n            @if $val-value {\n              // Set the value of our rule.\n              // If its a map, use val-value.content.\n\n              $val-slug: if(\n                meta.type-of($val-value) == \"map\",\n                map.get($val-value, \"slug\"),\n                $val-key\n              );\n\n              $value: if(\n                meta.type-of($val-value) == \"map\",\n                map.get($val-value, \"content\"),\n                $val-value\n              );\n\n              @if $val-slug == \"\" or smart-quote($val-slug) == \"noValue\" {\n                $no-value: true;\n              }\n\n              // Add any appended values...\n\n              @if map.get($utility, valueAppend) {\n                $value: $value + map.get($utility, valueAppend);\n              }\n\n              // ...or prepended values.\n\n              @if map.get($utility, valuePrepend) {\n                $value: map.get($utility, valuePrepend) + $value;\n              }\n\n              // And we'll set the $v as $val-slug for use in\n              // constructing the selector (.$b-$m-$v).\n\n              $v: $val-slug;\n\n              // -------- Start of Modifiers ----------\n\n              // Now we'll check for modifiers and loop through them\n              // to get the props we need to build our rule.\n\n              // Modifiers are held in a MAP,\n              // where each individual modifer has the keypair\n              // [slug]:[value]\n\n              // So, check for modifiers.\n\n              @if map.get($utility, modifiers) {\n                // If there are modifiers, capture them as $our-modifiers.\n\n                $our-modifiers: map.get($utility, modifiers);\n              } @else {\n                // If there aren't, build a dummy so we can keep\n                // all our build in the same loop.\n\n                $our-modifiers: (\n                  \"slug\": null,\n                );\n              }\n\n              // OK! C'mon, let's loop!\n              // https://www.youtube.com/watch?v=X9i2i07wPUw\n\n              // -------- For each modifier in $our-modifiers ----------\n\n              @each $mod-key, $mod-val in $our-modifiers {\n                $property: if(\n                  $mod-val == null or $mod-val == \"\",\n                  $property-default,\n                  multi-cat($property-default, $mod-val)\n                );\n\n                // Now we go through to set the $selector.\n\n                // If mod-props.slug is noModifier...\n\n                @if $mod-key ==\n                  \"\" or\n                  $mod-key ==\n                  slug or\n                  smart-quote($mod-key) ==\n                  \"noModifier\"\n                {\n                  // First, we can test to see if the base $b is null\n\n                  @if not $b {\n                    // If it _is_ null, the rule's selector is $v.\n\n                    $selector: $v;\n\n                    // if the value is noValue ('')\n                  } @else if $no-value {\n                    // selector is the base only\n\n                    $selector: $b;\n                  } @else {\n                    // otherwise, selctor is joined with a hyphen.\n\n                    $selector: $b + \"-\" + $v;\n\n                    // Nice! We just took care of the non-modifier cases!\n                  }\n                }\n\n                // If there _is_ a modifier...\n\n                @else {\n                  $mv: if($no-value, $mod-key, $mod-key + \"-\" + $v);\n\n                  // Once we have $mv, test for $b\n                  // and build the selector as before.\n\n                  $selector: if($b == null, $mv, $b + \"-\" + $mv);\n                }\n\n                // finished setting modifier vars\n\n                // Hey. Did we just finish $selector?\n                // And do we also have $property and $value?\n                // We do?!?!?! We do!\n\n                // FINALLY, 'BUILD THE RULE, MAX!'\n                // https://www.youtube.com/watch?v=R3Igz5SfBCE\n\n                @include render-utility(\n                  $utility,\n                  $selector,\n                  $property,\n                  $value,\n                  $val-value,\n                  $media-key\n                );\n              } // end the modifier loop\n            } // end the null value conditional\n          } // end the value loop\n        } // end the output conditional\n      }\n    } // end the uselist conditional\n  } // end the utility loop\n  // (ﾉ◕ヮ◕)ﾉ*:･ﾟ✧\n}\n\n@mixin render-utilities-in($utilities) {\n  @include these-utilities($utilities);\n\n  $our-breakpoints: map-deep-get($system-properties, breakpoints, standard);\n  @each $media-key, $media-value in $our-breakpoints {\n    @if map.get($theme-utility-breakpoints-complete, $media-key) {\n      @include at-media($media-key) {\n        @include these-utilities($utilities, $media-key);\n      }\n    }\n  }\n}\n\n/* style